@charset "UTF-8";
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Oswald";
  font-display: swap;
  src: url("../fonts/Oswaldregular.woff2") format("woff2"), url("../fonts/Oswaldregular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Oswald";
  font-display: swap;
  src: url("../fonts/Oswaldmedium.woff2") format("woff2"), url("../fonts/Oswaldmedium.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  position: relative;
  display: grid;
  width: 100%;
  min-width: 320px;
  min-height: 100vh;
  margin: 0 auto;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  color: #000000;
}

img {
  max-width: 100%;
  object-fit: cover;
  object-position: center;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
}

.container {
  position: relative;
  width: 100%;
  min-width: 320px;
  max-width: 1220px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .container {
    padding: 0;
  }
}

.title-page {
  margin: 0;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  color: #000000;
}
@media (min-width: 768px) {
  .title-page {
    font-size: 60px;
    line-height: 60px;
  }
}

.title-page-other {
  position: relative;
  margin: 0 -20px;
  overflow: hidden;
  font-weight: 400;
  font-size: 24px;
  line-height: 30px;
  font-family: "Oswald", "Arial", sans-serif;
}
@media (min-width: 768px) {
  .title-page-other {
    margin: 0 -30px;
    padding-left: 30px;
    font-size: 36px;
    line-height: 46px;
  }
}
@media (min-width: 1440px) {
  .title-page-other {
    margin: 0 -110px;
    padding-left: 70px;
  }
}

.title-page-other span {
  padding: 0 20px;
}
@media (min-width: 768px) {
  .title-page-other span {
    padding: 0 37px;
  }
}

.title-page-other--lines::after, .title-page-other--lines::before {
  content: "";
  position: absolute;
  top: 16px;
  display: inline-block;
  width: 100%;
  height: 2px;
  vertical-align: middle;
  background-color: #68b738;
}
@media (min-width: 768px) {
  .title-page-other--lines::after, .title-page-other--lines::before {
    top: 25px;
  }
}
.title-page-other--lines::before {
  display: none;
  margin-left: -100%;
}
@media (min-width: 768px) {
  .title-page-other--lines::before {
    display: inline-block;
  }
}
.title-page-other--lines::after {
  margin-right: -100%;
}

.header {
  position: relative;
  border-bottom: 1px solid #d9d9d9;
}
.header--index {
  border: none;
}
@media (min-width: 1440px) {
  .header--index {
    padding-bottom: 173px;
  }
}
@media (min-width: 768px) {
  .header {
    padding-top: 22px;
    border: none;
  }
}

.header .container {
  padding: 0;
}

.header__bigtitle {
  padding: 28px 20px 37px;
  border-top: 1px solid #ebebeb;
}
@media (min-width: 768px) {
  .header__bigtitle {
    padding: 56px 30px 41px;
    font-size: 60px;
    line-height: 60px;
    text-align: left;
    border: none;
  }
}
@media (min-width: 1440px) {
  .header__bigtitle {
    padding: 60px 0 40px;
    font-size: 60px;
    line-height: 60px;
    border: none;
  }
}

.header__bigtitle--form {
  padding-bottom: 3px;
}
@media (min-width: 768px) {
  .header__bigtitle--form {
    padding-bottom: 44px;
    padding-left: 70px;
  }
}
@media (min-width: 1440px) {
  .header__bigtitle--form {
    padding-left: 0;
  }
}

.header--form {
  border: none;
}

.header__subtitle {
  padding-top: 19px;
  padding-right: 50px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .header__subtitle {
    padding-top: 33px;
    font-size: 20px;
    line-height: 30px;
  }
}

@media (min-width: 1440px) {
  .header--index::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 50%;
    z-index: -1;
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/index-background-desktop.jpg");
    background-repeat: no-repeat;
    background-position: left;
    background-size: cover;
  }
}
@media (min-resolution: 2dppx) {
  .header--index::after {
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/index-background-desktop@2x.jpg");
  }
}

.button {
  display: block;
  width: 168px;
  margin: 0 auto;
  padding: 10px;
  font-size: 16px;
  line-height: 20px;
  font-family: "Oswald", "Arial", sans-serif;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #68b738;
  border: none;
  cursor: pointer;
}
.button:hover, .button:focus {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5eaa2f;
}

/*Модификатор для серых кнопок*/
.button--other {
  color: #444444;
  background-color: #f2f2f2;
}
.button--other:hover, .button--other:focus {
  background-color: #ebebeb;
}
.button--other:active {
  color: rgba(68, 68, 68, 0.3);
}

.navigation {
  position: relative;
  min-height: 65px;
  padding-top: 10px;
}
@media (min-width: 768px) {
  .navigation {
    display: grid;
    align-items: center;
    padding: 0 30px;
    gap: 20px;
    grid-template-columns: repeat(2, auto);
  }
}
@media (min-width: 1440px) {
  .navigation {
    padding: 31px 0 20px;
    grid-template-columns: repeat(2, 1fr);
  }
}

.navigation__logo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 191px;
  height: 38px;
  margin-left: 19px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .navigation__logo {
    width: 174px;
    height: 52px;
    margin-top: -6px;
    margin-left: 0;
  }
}
@media (min-width: 1440px) {
  .navigation__logo {
    width: 202px;
    height: 59px;
    margin-top: 1px;
  }
}

.navigation__logo img {
  display: block;
  width: 100%;
  height: 100%;
}

.navigation__btn {
  position: absolute;
  top: 23px;
  right: 20px;
  width: 24px;
  height: 18px;
  background: none;
  border: none;
  border-top: 2px solid #000000;
}
@media (min-width: 768px) {
  .navigation__btn {
    display: none;
  }
}
.navigation__btn::before, .navigation__btn::after {
  content: "";
  position: absolute;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.navigation__btn::before {
  top: 45%;
  transform: translateY(-50%);
}
.navigation__btn::after {
  bottom: 0;
}

.navigation--opened .navigation__btn {
  border-top: 2px solid transparent;
}
.navigation--opened .navigation__btn::before {
  transform: translateY(-50%) rotate(-46deg);
}
.navigation--opened .navigation__btn::after {
  top: 45%;
  transform: translateY(-50%) rotate(46deg);
}

.navigation__list {
  position: absolute;
  top: 65px;
  z-index: 10;
  display: none;
  flex-direction: column;
  width: 100%;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .navigation__list {
    position: static;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-end;
    background-color: transparent;
  }
}
@media (min-width: 1440px) {
  .navigation__list {
    margin-left: 3px;
  }
}

.navigation--opened .navigation__list {
  display: flex;
}

.navigation__item {
  border-top: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .navigation__item {
    margin-left: 36px;
    padding-top: 0;
    border: none;
  }
}
@media (min-width: 1440px) {
  .navigation__item {
    margin-left: 35px;
  }
}

.navigation__link {
  display: block;
  padding-top: 23px;
  padding-bottom: 22px;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-align: center;
  color: #000000;
  text-transform: uppercase;
  text-decoration: none;
}
.navigation__link:hover, .navigation__link:active, .navigation__link:focus {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .navigation__link {
    position: relative;
    padding: 10px 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
  }
}
@media (min-width: 1440px) {
  .navigation__link {
    padding: 6px 0;
    font-size: 20px;
    line-height: 30px;
  }
}

@media (min-width: 1440px) {
  .header--index .navigation__link {
    color: #ffffff;
  }
}

@media (min-width: 768px) {
  .navigation__link--current::before {
    content: "";
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 2px;
    background-color: #68b738;
  }
}

@media (min-width: 1440px) {
  .header--index .navigation__link--current::before {
    background-color: #ffffff;
    border-bottom: 2px solid #ffffff;
  }
}

.navigation__title {
  position: relative;
  padding: 28px 20px 37px;
}
@media (min-width: 768px) {
  .navigation__title {
    padding: 44px 30px 39px;
  }
}
@media (min-width: 1440px) {
  .navigation__title {
    padding: 61px 0 41px;
  }
}
.navigation__title::after, .navigation__title::before {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  border-bottom: 1px solid #ebebeb;
}
@media (min-width: 768px) {
  .navigation__title::after, .navigation__title::before {
    display: none;
  }
}
.navigation__title::before {
  top: 0;
  bottom: auto;
}

.navigation__title--form {
  padding-bottom: 16px;
}
@media (min-width: 768px) {
  .navigation__title--form {
    padding: 44px 70px 64px;
  }
}
@media (min-width: 1440px) {
  .navigation__title--form {
    padding: 60px 0 65px;
  }
}
.navigation__title--form::after {
  border-bottom: none;
}

.contact__map-image {
  display: none;
  width: 100%;
  height: 362px;
}

.contact__map-image img {
