  line-height: 18px;
  font-family: "Arial", sans-serif;
  color: #444444;
}
@media (min-width: 768px) {
  .advantage__item {
    margin-bottom: 0;
    padding: 111px 75px 50px 0;
    font-size: 16px;
    line-height: 24px;
  }
}
@media (min-width: 1440px) {
  .advantage__item {
    padding-right: 0;
  }
}
.advantage__item:last-child {
  margin-bottom: 0;
}
.advantage__item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 60px;
  height: 60px;
  background-color: #68b738;
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-width: 768px) {
  .advantage__item::before {
    width: 80px;
    height: 80px;
  }
}
@media (min-width: 768px) {
  .advantage__item::after {
    content: counter(subsection);
    position: absolute;
    top: 0;
    left: 110px;
    z-index: -1;
    margin: 0;
    font-size: 280px;
    line-height: 230px;
    font-family: "Oswald", "Arial", sans-serif;
    color: #f2f2f2;
    counter-increment: subsection;
  }
}

.advantage__item--beneficial::before {
  background-image: url("../img/svg/benefit-mobile.svg");
